@import "../../Variables.scss";

@keyframes show {
  from {
      bottom: 0;
      color: white;
  }
  to {
    bottom: -23px;
    color: red;
  }
}

@keyframes disabled {
    from {
        background-color: $primary-color;
    }
    to {
        background-color: lighten($color: $primary-color, $amount: 60);
    }
}

@keyframes undisabled {
    from {
        background-color: lighten($color: $primary-color, $amount: 60);
    }
    to {
        background-color: $primary-color;
    }
}


/* Safari */
@-webkit-keyframes spin {
  0% { -webkit-transform: rotate(0deg); }
  100% { -webkit-transform: rotate(360deg); }
}

@keyframes spin {
  0% { transform: rotate(0deg); }
  100% { transform: rotate(360deg); }
}


.container {
    width: calc(100vw - 10px);
    background-color: $primary-color;
    .inner-width {
        width: 100%;
        padding-bottom: 50px;
        .content {
            h1 {
                color: white;
                text-align: center;
                padding-top: 30px;
            }

            .form {
                border-radius: 5px;
                background-color: white;
                width: 80%;
                margin: 0 auto;
                display: flex;
                flex-direction: column;

                .required::after {
                    content: " * ";
                    color: red;
                    font-style: italic;
                    vertical-align: super;
                    font-size: 0.8rem;
                }

                .actions{
                    display: flex;
                    justify-content: center;
                    margin: 32px;
                    button{
                        height: 60px;
                        width: 100px;
                        border-radius: 5px;
                        border: 1px solid gray;
                        position: relative;
                        background-color: $primary-color;
                        cursor: pointer;
                        color: white;
                        animation: undisabled 0.3s ease-in;

                        &:disabled{
                            background-color: lighten($color: $primary-color, $amount: 60);
                            border: 0px solid gray;
                            animation: disabled 0.3s ease-in;
                        }

                        .loader {
                            position: absolute;
                            border: 5px solid #f3f3f3;
                            border-radius: 50%;
                            border-top: 5px solid $primary-color;
                            width: 20px;
                            height: 20px;
                            top: 28%;
                            left: 38%;
                            -webkit-animation: spin 2s linear infinite; /* Safari */
                            animation: spin 2s linear infinite;
                        }
                    }
                }

                .group {
                    margin: 2rem;
                    display: flex;
                    flex-wrap: wrap;
                    justify-content: space-between;
                    line-height: 1.5rem;

                    label {
                        min-width: 100px;
                    }

                    .input-field {
                        width: 300px;
                        position: relative;
                        display: block;

                        input {
                            height: 1.5rem;
                            padding: 5px;
                            border: 1px solid lightgray;
                            border-radius: 5px;

                            &:focus {
                                outline: none !important;
                                border-color: $primary-color;
                                box-shadow: 0 0 5px $primary-color;
                            }
                        }

                        textarea {
                            width: 100%;
                            padding: 5px;
                            border: 1px solid lightgray;
                            border-radius: 5px;
                            resize: none;
                            height: 100px;

                            &:focus {
                                outline: none !important;
                                border-color: $primary-color;
                                box-shadow: 0 0 5px $primary-color;
                            }
                        }

                        .error {
                            position: absolute;
                            bottom: -22px;
                            left: 2px;
                            color: red;
                            font-size: 0.7rem;
                            animation: show 0.3s ease-in;
                        }
                    }

                }
            }
        }
    }
}
